import React, { Component } from 'react'
import { connect } from 'react-redux';
import { push } from 'connected-react-router';
import { getSubmissions, selectRoot, selectError, SubmissionGrid, getForm } from 'react-formio';

const Submissions = class extends Component {
  componentWillMount() {
    this.props.getSubmissions(1);
  }

  render() {
    const {form, submissions, onAction, getSubmissions} = this.props

  return (
    <div>
       <SubmissionGrid
          submissions={submissions}
          form={form}
          onAction={onAction}
          getSubmissions={getSubmissions}
        />
    </div>
  );
  }
}
const mapStateToProps = (state, ownProps) => {
  const f = getForm('bslnotes');
  const submissions = selectRoot('submissions', state);

  return {
    form: f,
    submissions: submissions,
    isLoading: f.isActive || submissions.isActive,
    errors: [
      selectError('submissions', state),
      selectError('form', state)
    ]
  };
};


const mapDispatchToProps = (dispatch) => {
  return {
    getSubmissions: (page, query) => dispatch(getSubmissions('submissions', page, query, 'bslNotes')),
    onAction: (submission, action) => {
      switch(action) {
        case 'view':
        case 'row':
          dispatch(push(`/form/bslNotes/submission/${submission._id}`));
          break;
        case 'edit':
          dispatch(push(`/form/bslNotes/submission/${submission._id}/edit`));
          break;
        case 'delete':
          dispatch(push(`/form/bslNotes/submission/${submission._id}/delete`));
          break;
        default:
      }
    },
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Submissions)